export default {
  render(createElement) {
    return createElement('svg', {
      attrs: {
        class: 'svg-icon',
        viewBox: this.view,
        version: '1.1',
        xmlns: 'http://www.w3.org/2000/svg'
      },
      on: {
        click: this.click
      }
    }, this.path.map((i, index) => {
      return createElement('path', {
        attrs: {
          d: i,
          fill: this.colors[index]
        }
      });
    }));
  },
  props: {
    type: String,
    colors: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  methods: {
    click() {
      this.$emit('click');
    }
  },
  computed: {
    path() {
      if(this.type && Icon[this.type]) {
        return Icon[this.type].path;
      }
      return [];
    },
    view() {
      if(this.type && Icon[this.type]) {
        return Icon[this.type].view;
      }
      return '0 0 1024 1024';
    }
  }
};
</script>

<style>
  .svg-icon {
    vertical-align: middle;
    fill: currentColor;
    overflow: hidden;
  }
</style>


